import { Component, OnInit } from '@angular/core';
import { DataService } from '../../data.service';

@Component({
  selector: 'web-nav',
  templateUrl: './web-nav.component.html',
  styleUrls: ['./web-nav.component.css']
})
export class WebNavComponent implements OnInit {

  private sessionPass;
  private userPass;

  constructor(private dataService:DataService) { }

  ngOnInit() {
    this.onLoad('prepare', {}, () => {
      this.onLoad('user');
    });
  }

  onLoad(tag = 'prepare', arg = {}, callback = () => { }) {
    if (tag == 'user') {
      this.dataService.getUser(this.sessionPass.username)
        .subscribe((response) => {
          if (!response.status) {
            this.userPass = undefined;
            callback();
          } else {
            this.userPass = response.message[0];
            callback();
          }
        });
    }
    if (tag == 'session' || tag == 'prepare') {
      this.dataService.getSession()
        .subscribe((response) => {
          this.sessionPass = response.message;
          callback();
        });
    }
  }

  logout() {
    this.dataService.postLogout()
      .subscribe((response) => {
        this.refreshPage();
      });
  }

  refreshPage() {
    location.reload();
  }

}



