import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';

import { DataService } from '../../data.service';

@Component({
  selector: 'app-test1',
  templateUrl: './test1.component.html',
  styleUrls: ['./test1.component.css']
})
export class Test1Component implements OnInit {

  private level = 0;

  private sessionPass;
  private userPass;

  private problem: Array<any> = [];
  private problemCount = 10;

  constructor(private router: Router, private dataService: DataService) {
    for (let i = 0 ; i < this.problemCount ; ++i) {
      this.problem[i] = '1';
    }
  }

  ngOnInit() {
    window.scrollTo(0, 0);
    this.onLoad('prepare', {}, () => {
      this.onLoad('user', {}, () => {
        if (!this.sessionPass.isLogin) {
          this.router.navigate(['/']);
        }
      });
    });
  }

  onLoad(tag = 'prepare', arg = {}, callback = () => { }) {
    if (tag == 'user') {
      this.dataService.getUser(this.sessionPass.username)
        .subscribe((response) => {
          if (!response.status) {
            this.userPass = undefined;
            callback();
          } else {
            this.userPass = response.message[0];
            callback();
          }
        });
    }
    if (tag == 'session' || tag == 'prepare') {
      this.dataService.getSession()
        .subscribe((response) => {
          this.sessionPass = response.message;
          callback();
        });
    }
  }

  onAnswer() {
    if(confirm("นักเรียนต้องการส่งแบบทดสอบหรือไม่")) {
      this.dataService.postAnswer(this.level, this.problem)
        .subscribe((res) => {
          if (!res.status) {
            if (res.message == 'too') {
              alert('นักเรียนทำข้อสอบเกิน 10 ครั้งแล้ว');
              this.router.navigate(['/']);
            } else {
              alert('เกิดข้อผิดพลาด');
            }        
          } else {
            if (res.newhigh) {
              alert('ยินดีด้วยจ้าา นักเรียนได้คะแนน ' + res.ans + 'คะแนน และได้คะแนนสูงกว่าครั้งที่ผ่านมา');
              this.router.navigate(['/']);
            } else {
              alert('ยินดีด้วยจ้าา นักเรียนได้คะแนน ' + res.ans);
              this.router.navigate(['/']);
            }
          }
        });
    }
  }
}



