import { Component, OnInit } from '@angular/core';
import { DataService } from '../../data.service';

@Component({
  selector: 'app-course-show',
  templateUrl: './course-show.component.html',
  styleUrls: ['./course-show.component.css']
})
export class CourseShowComponent implements OnInit {

  private sessionPass;
  private userPass;

  private scoreUser: Array<any> = [ '', '', '', ''];

  constructor(private dataService: DataService) { }

  ngOnInit() {
    this.onLoad('prepare', [], () => {
      this.getScore(0);
      this.getScore(1);
      this.getScore(2);
      this.getScore(3);
    });
  }

  onLoad(tag = 'prepare', arg, callback = () => { }) {
    if (tag == 'user') {
      this.dataService.getUser(this.sessionPass.username)
        .subscribe((response) => {
          if (!response.status) {
            this.userPass = undefined;
            callback();
          } else {
            this.userPass = response.message;
            callback();
          }
        });
    }
    if (tag == 'session' || tag == 'prepare') {
      this.dataService.getSession()
        .subscribe((response) => {
          this.sessionPass = response.message;
          callback();
        });
    }
  }

  getScore(courseId) {
    let isFound = false;
    if (this.sessionPass.isLogin) {
      this.onLoad('user', [], () => {
        if (typeof this.userPass !== 'undefined') {
          isFound = true;
          this.scoreUser[courseId] = this.userPass[0].course[courseId];
        } 
        if (!isFound) {
          this.scoreUser[courseId] = 'ไม่สามารถค้นหาได้';
        }
      });
    } else {
      this.scoreUser[courseId] = 'คุณยังไม่ได้เข้าสู่ระบบ';
    }
  }

}



