import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { DataService } from '../data.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  private username = '';
  private password = '';
  private token = '';

  private error = undefined;

  private sessionPass;

  constructor(private router:Router, private dataService:DataService) { }

  ngOnInit() {
    window.scrollTo(0, 0);
    this.onLoad('session');
  }

  onLoad(tag = 'prepare', arg = {}, callback = () => { }) {
    if (tag == 'session' || tag == 'prepare') {
      this.dataService.getSession()
        .subscribe((response) => {
          this.sessionPass = response.message;
          if (response.message.isLogin) {
            this.router.navigate(['/']);
          }
          callback();
        });
    }
  }

  onLogin() {
    this.dataService.postLogin(this.username, this.password, this.token)
      .subscribe((response) => {
        if (!response.status) {
          this.onLoad('session', {}, () => {
            if (response.message == 'bad input') {
              this.error = 'รหัสยืนยันตัวผิดพลาด';
              this.token = '';
            } else {
              this.error = 'ไม่พบบัญชีนี้ในระบบ';
              this.password = '';
              this.token = '';
            }
          });
        } else {
          location.reload();
        }
      });
  }

}



