import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { DataService } from '../data.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  private username = '';
  private password = '';

  private name = '';
  private lastname = '';
  private studentNumber = '0';
  private studentRoom = '';
  private studentClass ='';

  private error = '';
  private errorInfo = '';

  private sessionPass;
  private userPass;

  constructor(private router: Router,private dataService:DataService) { }

  ngOnInit() {
    window.scrollTo(0, 0);
    this.onLoad('prepare', {}, () => {
      this.onLoad('user', {}, () => {
        if (!this.sessionPass.isLogin) {
          this.router.navigate(['/']);
        } else {
          this.username = this.userPass.username;
          this.name = this.userPass.name;
          this.lastname = this.userPass.lastname;
          this.studentClass = this.userPass.class;
          this.studentRoom = this.userPass.room;
          this.studentNumber = this.userPass.no;
        }
      });
    });
  }

  onLoad(tag = 'prepare', arg = {}, callback = () => { }) {
    if (tag == 'user') {
      this.dataService.getUser(this.sessionPass.username)
        .subscribe((response) => {
          if (!response.status) {
            this.userPass = undefined;
            callback();
          } else {
            this.userPass = response.message[0];
            callback();
          }
        });
    }
    if (tag == 'session' || tag == 'prepare') {
      this.dataService.getSession()
        .subscribe((response) => {
          this.sessionPass = response.message;
          callback();
        });
    }
  }

  changePwd() {
    this.dataService.postUpdate(this.username, this.password)
      .subscribe((res) => {
        if (!res.status) {
          if (res.message == 'bad input') {
            this.error = '4 ≤ Username, Password ≤ 20';
          } else {
            this.error = 'Username นี้มีในระบบแล้ว';
          }
        } else {
          location.reload();
        }
      });
  }
  
  changeInfo() {
    this.dataService.postUpdateInfo(this.name, this.lastname, this.studentClass, this.studentRoom, this.studentNumber)
      .subscribe((res) => {
        if (!res.status) {
          this.errorInfo = 'กรอกข้อมูลให้ครบ';
        } else {
          location.reload();
        }
      });
  }

}



