import { Injectable } from '@angular/core';
import { Http } from '@angular/http';

import * as encrypt from 'js-sha512';

import 'rxjs/add/operator/map';

@Injectable()
export class DataService {

  constructor(private http :Http) { }

  getSession() {
    return this.http.post('api/getsession', {})
      .map((res) => res.json());
  }

  getUser(name) {
    return this.http.post('api/search', {username: name})
      .map((res) => res.json());
  }

  postLogout() {
    return this.http.post('api/logout', {})
      .map((res) => res.json());
  }

  postLogin(user, pwd, cap) {
    return this.http.post('api/login', {username: user, password: encrypt(pwd), captcha: cap})
      .map((res) => res.json());
  }

  postUpdate(user, pwd) {
    return this.http.post('api/update', {username: user, password: encrypt(pwd)})
      .map((res) => res.json());
  }

  postUpdateInfo(nme, lastnme, c, r, n) {
    return this.http.post('api/updateinfo', {name: nme, lastname: lastnme, class: c, room: r, no: n})
      .map((res) => res.json());
  }

  postAnswer(whe, ans) {
    return this.http.post('api/answer', {where: '' +whe, answer: ans})
      .map((res) => res.json());
  }

}



