import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';

import { RouterModule, Routes } from '@angular/router';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { HomeComponent } from './home/home.component';

import { DataService } from './data.service';
import { WebNavComponent } from './sub/web-nav/web-nav.component';
import { CourseComponent } from './course/course.component';
import { NavJumbotronComponent } from './sub/nav-jumbotron/nav-jumbotron.component';
import { ProfileComponent } from './sub/profile/profile.component';
import { FooterComponent } from './sub/footer/footer.component';
import { CourseShowComponent } from './sub/course-show/course-show.component';
import { GeneticsComponent } from './content/genetics/genetics.component';
import { LoginComponent } from './login/login.component';
import { Test1Component } from './problem/test1/test1.component';
import { MaterialComponent } from './content/material/material.component';
import { Test2Component } from './problem/test2/test2.component';
import { Material2Component } from './content/material-2/material-2.component';
import { Material3Component } from './content/material-3/material-3.component';
import { Material4Component } from './content/material-4/material-4.component';
import { Material5Component } from './content/material-5/material-5.component';
import { EngineeringComponent } from './content/engineering/engineering.component';
import { EvolutionComponent } from './content/evolution/evolution.component';
import { Test3Component } from './problem/test3/test3.component';
import { Test4Component } from './problem/test4/test4.component';

const appRoutes: Routes = [
  {
    path: '',
    component: HomeComponent
  },
  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: 'dashboard',
    component: DashboardComponent
  },
  {
    path: 'course',
    component: CourseComponent
  },
  {
    path: 'course/@genetics',
    component: GeneticsComponent
  },
  {
    path: 'test/@genetics',
    component: Test1Component
  },
  {
    path: 'course/@genetic-material',
    component: MaterialComponent
  },
  {
    path: 'test/@genetic-material',
    component: Test2Component
  },
  {
    path: 'test/@genetic-engineering',
    component: Test3Component
  },
  {
    path: 'test/@evolution',
    component: Test4Component
  },
  {
    path: 'course/@genetic-material-2',
    component: Material2Component
  },
  {
    path: 'course/@genetic-material-3',
    component: Material3Component
  },
  {
    path: 'course/@genetic-material-4',
    component: Material4Component
  },
  {
    path: 'course/@genetic-material-5',
    component: Material5Component
  }, {
    path: 'course/@genetic-engineering',
    component: EngineeringComponent
  }, {
    path: 'course/@evolution',
    component: EvolutionComponent
  }
  
];

@NgModule({
  declarations: [
    AppComponent,
    DashboardComponent,
    HomeComponent,
    WebNavComponent,
    CourseComponent,
    NavJumbotronComponent,
    ProfileComponent,
    FooterComponent,
    CourseShowComponent,
    GeneticsComponent,
    LoginComponent,
    Test1Component,
    MaterialComponent,
    Test2Component,
    Material2Component,
    Material3Component,
    Material4Component,
    Material5Component,
    EngineeringComponent,
    EvolutionComponent,
    Test3Component,
    Test4Component
  ],
  imports: [
    RouterModule.forRoot(appRoutes),
    BrowserModule,
    FormsModule,
    HttpModule
  ],
  providers: [
    DataService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



